.loginPage{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
    color: black;
}

.form{
    display: flex;
    flex-direction: column;
    background-color: darkgrey;
    padding: 2rem 4rem;
    border: .3rem solid black;
    border-radius: 1rem;
}

.form input {
    margin-block: 10px;
    padding: 5px;
    border-radius: .5rem;
}

.form label{
    text-decoration: underline;
    margin-block: 10px;
    font-weight: bold;
}

.submit{
    font-weight: bold;
}

.submit:hover{
    color: rgb(51, 51, 51);
    background-color: rgb(212, 212, 212) ;
}

.instructions{
    margin: 5px auto;
    background-color: black;
    padding: 3px 10px;
    color: white;
    border-radius: .5rem;
}

.errMsg{
    font-weight: bold;
    margin: 5px auto;
    background-color: black;
    padding: 10px 20px;
    color: orange;
    border-radius: .5rem;
}

.success{
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    justify-content: center;
    align-items: center;
}

.successInner{
    position: relative;
    color: white;
    width:50%;
    height: 50%;
    background-color: rgba(0, 0, 0, 0.75);
    border: .5rem solid white;
    border-radius: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
}


